      <div class="chart-main">
        <ve-line height="360px" :colors="['#FF0000', '#FF7F00', '#FFFF00', '#00FF00', '#00FFFF', '#0000FF', '#8B00FF']" :data="chartDataVisitTrend" :legend="{ left: 0, bottom: 0 }"></ve-line>
