        <el-dropdown @command="chooseRetainKind">
          <span style="font-size: 20px; font-weight: 500; color: #000;" class="el-dropdown-link"> {{ visitRetainKind === 1 ? '新增用户留存' : '活跃用户留存' }}<i class="el-icon-arrow-down el-icon--right"></i> </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item :command="1">新增用户留存</el-dropdown-item>
