        <div class="section-chart-op">
          <el-date-picker v-model="dateVisitRetain" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
          <span
            class="date-shortcut"
            v-bind:class="{ active: isAcitveVisitRetain === 7 }"
            v-on:click="
              () => {
                dateVisitRetain = [yesterday().add(-7, 'days'), yesterday()];
