      <div>
        <el-table :data="visitRetainKind === 1 ? tableDataVisitRetainNew : tableDataVisitRetain">
          <el-table-column :width="(visitRetainKind === 1 ? tableDataVisitRetainNew : tableDataVisitRetain).length ? (index === 0 ? '100px' : 'auto') : 'auto'" v-for="(item, index) in visitRetainKind === 1 ? tableControlVisitRetainNew : tableControlVisitRetain" :key="index" :prop="item.prop" :label="item.label"></el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 用户画像 -->
    <div class="container section">
      <div class="section-chart-title">
        <el-dropdown @command="chooseUserPortraitKind">
          <span style="font-size: 20px; font-weight: 500; color: #000;" class="el-dropdown-link"> {{ userPortraitKind === 1 ? '新增用户画像' : '活跃用户画像' }}<i class="el-icon-arrow-down el-icon--right"></i> </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item :command="1">新增用户画像</el-dropdown-item>
            <el-dropdown-item :command="2">活跃用户画像</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <div class="section-chart-op">
          <span class="date-shortcut" v-bind:class="{ active: userPortraitDateKind === 1 }" @click="userPortraitDateKind = 1">昨天</span>
          <span class="date-shortcut" v-bind:class="{ active: userPortraitDateKind === 2 }" @click="userPortraitDateKind = 2">最近7天</span>
          <span class="date-shortcut" v-bind:class="{ active: userPortraitDateKind === 3 }" @click="userPortraitDateKind = 3">最近30天</span>
        </div>
      </div>
      <div>
        <el-row gutter="30">
          <el-col span="12">
            <el-table border max-height="266" :data="userPortraitKind === 1 ? dataUserPortraitProvinceNew : dataUserPortraitProvince">
              <el-table-column prop="name" label="省份"></el-table-column>
              <el-table-column prop="value" label="用户数"></el-table-column>
            </el-table>
          </el-col>
          <el-col span="12">
            <el-table border max-height="266" :data="userPortraitKind === 1 ? dataUserPortraitCityNew : dataUserPortraitCity">
              <el-table-column prop="name" label="城市"></el-table-column>
              <el-table-column prop="value" label="用户数"></el-table-column>
            </el-table>
          </el-col>
        </el-row>
        <el-row gutter="30" style="margin-top: 30px;">
          <el-col span="12">
            <ve-line height="360px" :colors="['#FF0000', '#FF7F00']" :data="userPortraitKind === 1 ? dataUserPortraitAgesNew : dataUserPortraitAges" :legend="{ left: 0, bottom: 0 }"></ve-line>
          </el-col>
          <el-col span="12">
            <ve-histogram height="360px" :colors="['#FF7F00']" :data="userPortraitKind === 1 ? dataUserPortraitGendersNew : dataUserPortraitGenders" :legend="{ left: 0, bottom: 0 }"></ve-histogram>
          </el-col>
        </el-row>

        <el-row gutter="30" style="margin-top: 30px;">
          <el-col span="12">
            <el-table style="margin-top: 56px;" border max-height="266" :data="userPortraitKind === 1 ? dataUserPortraitDevicesNew : dataUserPortraitDevices">
              <el-table-column prop="name" label="设备"></el-table-column>
              <el-table-column prop="value" label="用户数"></el-table-column>
            </el-table>
          </el-col>
          <el-col span="12">
            <ve-histogram height="360px" :colors="['#FF7F00']" :data="userPortraitKind === 1 ? dataUserPortraitPlatformsNew : dataUserPortraitPlatforms" :legend="{ left: 0, bottom: 0 }"></ve-histogram>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { M } from '../../../service/grpc.js';
import { rpc } from '../../../service/index.js';
import { getToken } from '../../../service/helper.js';
import VeLine from 'v-charts/lib/line.common';
import VeBar from 'v-charts/lib/bar.common';
import VePie from 'v-charts/lib/pie.common';
import VeHistogram from 'v-charts/lib/histogram.common';
import moment from 'moment';

const yesterday = () => {
  const temp = moment(new Date()).add(-1, 'days');
  return moment([temp.year(), temp.month(), temp.date()]);
};

export default {
  components: {
    VeLine,
    VeBar,
    VePie,
    VeHistogram
  },
  data() {
    return {
      yesterday,

      dateDailySummary: [yesterday().add(-7, 'days'), yesterday()],
      isActiveDailySummary: 7,
      chartDataDailySummary: {
        columns: ['日期', '累计用户数', '转发人数', '转发次数'],
        rows: []
      },

      dateVisitPage: [yesterday().add(-7, 'days'), yesterday()],
      isActiveVisitPage: 7,
      tableDataVisitPage: [],

      dateVisitSource: [yesterday().add(-7, 'days'), yesterday()],
      isAcitveVisitSource: 7,
      tableDataVisitSource: [],

      dateVisitStaytime: [yesterday().add(-7, 'days'), yesterday()],
      isAcitveVisitStaytime: 7,
      tableDataVisitStaytime: [],

      dateVisitDepth: [yesterday().add(-7, 'days'), yesterday()],
      isAcitveVisitDepth: 7,
      tableDataVisitDepth: [],

      dateVisitTrend: [yesterday().add(-7, 'days'), yesterday()],
      isAcitveVisitTrend: 7,
      chartDataVisitTrend: {
        columns: ['日期', '打开次数', '页面访问次数', '访问人数', '新用户数', '人均停留时长(秒)', '次均停留时长(秒)', '平均访问深度'],
        rows: []
      },

      dateVisitRetain: [yesterday().add(-7, 'days'), yesterday()],
      isAcitveVisitRetain: 7,
      tableDataVisitRetain: [],
      tableDataVisitRetainNew: [],
      tableControlVisitRetain: [],
      tableControlVisitRetainNew: [],
      visitRetainKind: 1,

      userPortraitKind: 1,
      userPortraitDateKind: 3,

      dataUserPortraitAges: {
        columns: ['年龄', '用户数'],
        rows: []
      },
      dataUserPortraitPlatforms: {
        columns: ['终端', '用户数'],
        rows: []
      },
      dataUserPortraitGenders: {
        columns: ['性别', '用户数'],
        rows: []
      },
      dataUserPortraitCity: [],
      dataUserPortraitDevices: [],
      dataUserPortraitProvince: [],

      dataUserPortraitAgesNew: {
        columns: ['年龄', '用户数'],
        rows: []
      },
      dataUserPortraitPlatformsNew: {
        columns: ['终端', '用户数'],
        rows: []
      },
      dataUserPortraitGendersNew: {
        columns: ['性别', '用户数'],
        rows: []
      },
      dataUserPortraitCityNew: [],
      dataUserPortraitDevicesNew: [],
      dataUserPortraitProvinceNew: []
    };
  },
  created() {
    this.token = getToken();

    this.dailySummary();
    this.visitPage();
    this.visitRetain();
    this.visitTrend();
    this.visitDepth();
    this.visitStaytime();
    this.visitSource();
    this.userPortrait();
  },
  watch: {
    dateDailySummary() {
      this.dailySummary();
    },
    dateVisitPage() {
      this.visitPage();
    },
    dateVisitSource() {
      this.visitSource();
    },
    dateVisitStaytime() {
      this.visitStaytime();
    },
    dateVisitDepth() {
      this.visitDepth();
    },
    dateVisitTrend() {
      this.visitTrend();
    },
    dateVisitRetain() {
      this.visitRetain();
    },
    userPortraitDateKind() {
      this.userPortrait();
    }
  },
  methods: {
    chooseUserPortraitKind(c) {
      this.userPortraitKind = c;
    },
    chooseRetainKind(c) {
      this.visitRetainKind = c;
    },
    copy(v) {
      return JSON.parse(JSON.stringify(v));
    },
    formatDate(str) {
      return `${str.slice(0, 4)}-${str.slice(4, 6)}-${str.slice(6, 8)}`;
    },
    userPortrait() {
      rpc(
        'StoreAnalysisService.GetAnalysisUserPortrait',
        M('AnalysisUserPortraitKind', {
          token: M('Token', { token: this.token }),
          kind: this.userPortraitDateKind
        })
      ).then(res => {
        const dataUserPortrait = JSON.parse(res.jsonstring).visit_uv;
        const dataUserPortraitNew = JSON.parse(res.jsonstring).visit_uv_new;

        this.dataUserPortraitProvinceNew = dataUserPortraitNew.province;
        this.dataUserPortraitCityNew = dataUserPortraitNew.city;
        this.dataUserPortraitDevicesNew = dataUserPortraitNew.devices;
        this.dataUserPortraitAgesNew.rows = dataUserPortraitNew.ages.map(v => ({ 年龄: v.name, 用户数: v.value }));
        this.dataUserPortraitGendersNew.rows = dataUserPortraitNew.genders.map(v => ({ 性别: v.name, 用户数: v.value }));
        this.dataUserPortraitPlatformsNew.rows = dataUserPortraitNew.platforms.map(v => ({ 终端: v.name, 用户数: v.value }));

        this.dataUserPortraitProvince = dataUserPortrait.province;
        this.dataUserPortraitCity = dataUserPortrait.city;
        this.dataUserPortraitDevices = dataUserPortrait.devices;
        this.dataUserPortraitAges.rows = dataUserPortrait.ages.map(v => ({ 年龄: v.name, 用户数: v.value }));
        this.dataUserPortraitGenders.rows = dataUserPortrait.genders.map(v => ({ 性别: v.name, 用户数: v.value }));
        this.dataUserPortraitPlatforms.rows = dataUserPortrait.platforms.map(v => ({ 终端: v.name, 用户数: v.value }));
      });
    },
    visitRetain() {
      const begin = moment(this.dateVisitRetain[0]);
      const end = moment(this.dateVisitRetain[1]);

      rpc(
        'StoreAnalysisService.GetAnalysisVisitRetainList',
        M('AnalysisTime', {
          token: M('Token', { token: this.token }),
          createdatgreaterthan: begin.add(1, 'days').unix(),
          createdatlessthan: end.add(2, 'days').unix()
        })
      ).then(res => {
        const tableDataVisitRetain = res.analysisvisitretainlistList
          .map(item => {
            item.visituv = JSON.parse(item.visituvjsonstring).sort((a, b) => a.key > b.key);
            item.visituvnew = JSON.parse(item.visituvnewjsonstring).sort((a, b) => a.key > b.key);
            item.refdate = this.formatDate(item.refdate);
            return item;
          })
          .sort((a, b) => a.refdate > b.refdate);

        this.tableControlVisitRetain = [{ label: '日期', prop: 'refdate' }].concat(
          tableDataVisitRetain.map((item, index) => {
            if (index === 0) {
              return { label: '活跃用户数', prop: `${index}` };
            } else {
              return { label: `${index}日后`, prop: `${index}` };
            }
          })
        );

        this.tableControlVisitRetainNew = [{ label: '日期', prop: 'refdate' }].concat(
          tableDataVisitRetain.map((item, index) => {
            if (index === 0) {
              return { label: '新增用户数', prop: `${index}` };
            } else {
              return { label: `${index}日后`, prop: `${index}` };
            }
          })
        );

        this.tableDataVisitRetain = this.copy(tableDataVisitRetain).map(item => {
          for (let i = 0; i < tableDataVisitRetain.length; i += 1) {
            if (i === 0) {
              item[i] = item.visituv[i] ? item.visituv[i].value : 0;
            } else {
              const v = item.visituv[0] ? item.visituv[0].value : 0;
              const v1 = item.visituv[i] ? item.visituv[i].value : 0;
              const v2 = (v1 / v) * 100;
              item[i] = v2 ? `${v2.toFixed(2)}%` : '0%';
            }
          }
          return item;
        });

        this.tableDataVisitRetainNew = this.copy(tableDataVisitRetain).map(item => {
          for (let i = 0; i < tableDataVisitRetain.length; i += 1) {
            if (i === 0) {
              item[i] = item.visituvnew[i] ? item.visituvnew[i].value : 0;
            } else {
              const v = item.visituvnew[0] ? item.visituvnew[0].value : 0;
              const v1 = item.visituvnew[i] ? item.visituvnew[i].value : 0;
              const v2 = (v1 / v) * 100;
              item[i] = v2 ? `${v2.toFixed(2)}%` : '0%';
            }
          }
          return item;
        });
      });
    },
    visitTrend() {
      const begin = moment(this.dateVisitTrend[0]);
      const end = moment(this.dateVisitTrend[1]);

      rpc(
        'StoreAnalysisService.GetAnalysisVisitTrendList',
        M('AnalysisTime', {
          token: M('Token', { token: this.token }),
          createdatgreaterthan: begin.unix(),
          createdatlessthan: end.add(2, 'days').unix()
        })
      ).then(res => {
        let rows0 = res.analysisvisittrendlistList.map(v => ({
          日期: this.formatDate(v.refdate),
          打开次数: v.sessioncnt,
          页面访问次数: v.visitpv,
          访问人数: v.visituv,
          新用户数: v.visituvnew,
          '人均停留时长(秒)': v.staytimeuv,
          '次均停留时长(秒)': v.staytimesession,
          平均访问深度: v.visitdepth
        }));

        let rows = [];

        for (let b = begin; b.isBefore(end); b.add(1, 'days')) {
          rows.push(
            rows0.find(v => v['日期'] == b.format('YYYY-MM-DD')) || {
              日期: b.format('YYYY-MM-DD'),
              打开次数: 0,
              页面访问次数: 0,
              访问人数: 0,
              新用户数: 0,
              '人均停留时长(秒)': 0,
              '次均停留时长(秒)': 0,
              平均访问页面深度: 0
            }
          );
        }

        rows.pop();

        this.chartDataVisitTrend.rows = rows;
      });
    },
    visitDepth() {
      const begin = moment(this.dateVisitDepth[0]);
      const end = moment(this.dateVisitDepth[1]);

      rpc(
        'StoreAnalysisService.GetAnalysisVisitDepthList',
        M('AnalysisTime', {
          token: M('Token', { token: this.token }),
          createdatgreaterthan: begin.unix(),
          createdatlessthan: end.add(2, 'days').unix()
        })
      ).then(res => {
        this.tableDataVisitDepth = res.analysisvisitdepthlistList;
      });
    },
    visitStaytime() {
      const begin = moment(this.dateVisitStaytime[0]);
      const end = moment(this.dateVisitStaytime[1]);

      rpc(
        'StoreAnalysisService.GetAnalysisVisitStaytimeList',
        M('AnalysisTime', {
          token: M('Token', { token: this.token }),
          createdatgreaterthan: begin.unix(),
          createdatlessthan: end.add(2, 'days').unix()
        })
      ).then(res => {
        this.tableDataVisitStaytime = res.analysisvisitstaytimelistList;
      });
    },
    visitSource() {
      const begin = moment(this.dateVisitSource[0]);
      const end = moment(this.dateVisitSource[1]);

      rpc(
        'StoreAnalysisService.GetAnalysisVisitSourceList',
        M('AnalysisTime', {
          token: M('Token', { token: this.token }),
          createdatgreaterthan: begin.unix(),
          createdatlessthan: end.add(2, 'days').unix()
        })
      ).then(res => {
        this.tableDataVisitSource = res.analysisvisitsourcelistList;
      });
    },
    visitPage() {
      const begin = moment(this.dateVisitPage[0]);
      const end = moment(this.dateVisitPage[1]);

      rpc(
        'StoreAnalysisService.GetAnalysisVisitPageList',
        M('AnalysisTime', {
          token: M('Token', { token: this.token }),
          createdatgreaterthan: begin.unix(),
          createdatlessthan: end.add(2, 'days').unix()
        })
      ).then(res => {
        this.tableDataVisitPage = res.analysisvisitpagelistList;
      });
    },
    dailySummary() {
      const begin = moment(this.dateDailySummary[0]);
      const end = moment(this.dateDailySummary[1]);
      rpc(
        'StoreAnalysisService.GetAnalysisDailySummaryList',
        M('AnalysisTime', {
          token: M('Token', { token: this.token }),
          createdatgreaterthan: begin.unix(),
          createdatlessthan: end.add(2, 'days').unix()
        })
      ).then(res => {
        let rows0 = res.analysisdailysummarylistList.map(v => ({
          日期: this.formatDate(v.refdate),
          累计用户数: v.visittotal,
          转发人数: v.shareuv,
          转发次数: v.sharepv
        }));

        let rows = [];

        for (let b = begin; b.isBefore(end); b.add(1, 'days')) {
          rows.push(
            rows0.find(v => v['日期'] == b.format('YYYY-MM-DD')) || {
              日期: b.format('YYYY-MM-DD'),
              累计用户数: 0,
              转发人数: 0,
              转发次数: 0
            }
          );
        }

        rows.pop();

        this.chartDataDailySummary.rows = rows;
      });
    }
  }
};
</script>

